.Modal__Modal__hash-base64-5 {
        position: fixed;
        z-index: 500;
        /* Backdrop.css has z-index 100 */
        background-color: white;
        width: 70%;
        border: 1px solid #ccc;
        box-shadow: 1px 1px 1px black;
        padding: 16px;
        left: 15%;
        top: 30%;
        box-sizing: border-box;
        transition: all 0.3s ease-out;
}

@media (min-width: 600px) {
        .Modal__Modal__hash-base64-5 {
                width: 500px;
                left: calc(50% - 250px);
        }
}